.Card {
  border-left: 1px solid rgba(0,0,0,0.1);
  padding: 0;
  width: 85.60mm;
  height: 53.98mm;

  -ms-flex: 0 1;

      flex: 0 1;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: justify;
      justify-content: space-between;
  background-color: white;
  -webkit-box-shadow: 5px, 5px, 5px, 5px, rgba(0,0,0,0.5);
          box-shadow: 5px, 5px, 5px, 5px, rgba(0,0,0,0.5);
}

.Card:first-child {
  border-left: none;
}

.Card .CardBody {
  padding: 0.5em;
  display: -ms-flexbox;
  display: flex;
  -ms-flex: 1 1;
      flex: 1 1;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-pack: justify;
      justify-content: space-between;  
}

.did {
  -ms-flex: 0 1;
      flex: 0 1;
  font-family: 'Source Code Pro', monospace;
  font-size: 6pt;
  color: white;
  padding: 0.5em;
  background-color: #5c50ca;
}
.Bio {
  text-align: left;
  font-size: 8pt;
}
.Bio strong {
  color: #5c50ca;
}
.Blurb {
  text-align: left;
  font-size: 6pt;
  padding: 0.5em;
}
.Blurb h3 {
  color: red;
}

.Seed {
  text-align: left;
  font-size: 6pt;
  color: red;
  padding: 0.5em;
}
@media print {
  .Card {
    width: 85.60mm;
    height: 53.98mm;
  }
}



